/* frontend/src/styles/styles.css */

/* Global styles */


body {
  background-image: url('https://e1.pxfuel.com/desktop-wallpaper/734/564/desktop-wallpaper-techy-backgrounds-portfolio.jpg');
  background-size: cover;
  margin: 0;
  font-family: Arial, sans-serif;
}

/* Add these styles to your existing CSS file */
.contact-image {
  max-width: 25%;
  height: auto;
  margin-bottom: 10px; /* Adjust spacing as needed */
}

.contact-container {
  text-align: center;
  margin-top: 50px; /* Adjust the margin as needed */
}

.contact-list {
  list-style-type: none;
  padding: 0;
  display: flex; /* Add flexbox */
  justify-content: center; /* Center items horizontally */
  align-items: center; /* Center items vertically */
}

.contact-list li {
  margin-right: 20px; /* Add margin between items */
}

.contact-list li:last-child {
  margin-right: 0; /* Remove margin from the last item */
}

.contact-list li a {
  font-size: 24px;
  display: flex; /* Add flexbox to align icon and text */
  align-items: center; /* Center items vertically */
}
.social-icon {
  width: 50px;
  height: auto; /* Maintain aspect ratio */
  margin-right: 10px; /* Adjust margin between icon and text */
}

.text-box {
  color: white;
}

.projects-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; /* Align items to the start of the flex container */
  padding: 20px;
}

.project-list {
  width: auto; /* Set width to auto to adjust based on content */
  margin-right: 20px; /* Add right margin to create space between project list and view button */
}

.project-list ul {
  list-style-type: none;
  padding: 0;
}

.project-list li {
  margin-bottom: 10px;
}

.project-name {
  color: white; /* White color for shining effect */
  font-weight: bold; /* Optional: Make the text bold */
}

.project-name-box {
  padding: 10px;
  border: 2px solid white;
  border-radius: 5px;
  font-weight: bold; /* Add this line */
}

.view-button {
  flex-shrink: 0; /* Prevent the view button from shrinking */
}

.view-button button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.view-button button:hover {
  background-color: #0056b3;
}


/* Header styles */
.header {
  text-align: center;
  padding: 20px;
}

/* Menu styles */
.menu {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.menu-items {
  display: flex;
  list-style-type: none;
  padding: 0;
}

.menu-item {
  margin: 0 10px;
  font-size: 20px; /* Adjust the font size as needed */
  border: 2px solid #fff;
  padding: 10px 15px;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.3); /* Background color for the menu items */
}
.center {
  text-align: center;

}
.menu-item a {
  text-decoration: none;
  color: #fff; /* Link color */
}
/* Add this to your existing CSS file */
.photon {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0;
  animation: cometAnimation 4s infinite;
}
/* Add this CSS to your styles.css or another appropriate CSS file */

.go-back-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.go-back-link {
  display: inline-block;
  padding: 10px 20px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
  text-decoration: none;
  color: #333;
  transition: background-color 0.3s ease;
}

.go-back-link:hover {
  background-color: #e0e0e0;
}


@keyframes cometAnimation {
  0% {
    transform: translate(0, 0);
    opacity: 1;
  }
  100% {
    transform: translate(200px, -200px);
    opacity: 0;
  }
}



